.body{
    margin: auto;
    height: 100vh;
    
}


.wrapper{

    background: linear-gradient(270deg, #0ff549, #16ffcd);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
      
}

.main {
    height: 90vh;
    background: #fff;
    box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
    width: 80%;
    margin: 0 auto;
  }



  .title-container {
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 10px 10px 10px 10px;
    color: #000;
  }

  .form-container {
    background-color: #e9eaeb67;
    height: 90vh;
    padding-top: 100px;
    padding-left: 50px;
    box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  }

  input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 1px #000000;
    width: 30%;
    padding-bottom: 4px;
    color: #fff !important;
    font-weight: lighter;
    letter-spacing: 2px;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
  }

  input[type="text"] { 
    outline: none;
}
button {
    border: 0;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 15px;
    cursor: pointer;
    background-color: #16ffcd;
    border: 2px solid #000;
    color: #000000;
    font-weight: 100;
  }

  @media only screen and (max-width: 600px) {
    .form-container {
        background-color: #e9eaeb67;
        height: 55vh;
    }

    .title-container
    {
            height: 30vh;
    }

    input[type="text"] {
  
        font-size: 10px;
        letter-spacing: 0px;

  }

}